<template>
  <div class="center-map" ref="mapRef"></div>
</template>

<script>
export default {
  data() {
    return {
      chartRef: null,
      selectedProvince: null,
    };
  },
  async mounted() {
    await this.$nextTick(this.initChart);
    window.addEventListener("resize", this.resizeChart);
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.resizeChart);
    this.chartRef && this.chartRef.dispose();
  },
  methods: {
    resizeChart() {
      this.chartRef && this.chartRef.resize();
    },
    initChart() {
      this.chartRef = this.$echarts.init(this.$refs.mapRef);
      const option = {
        backgroundColor: "transparent",
        tooltip: {
          trigger: "item",
          backgroundColor: "transparent", // 0 0 0 .5
          // borderColor: "#00ffcc",
          formatter(params) {
            if ("name" in params && params.name) {
              return `
                <div class="customer_map_tooltip">
                  <div class="title"><div class="title-icon"></div>${params.name}</div>
                  <p class="second_line">
                    <span class="yellow">名单数量：<a>22个</a></span>
                    <span class="yellow">客户数量：<a>22个</a></span>
                  </p>
                  <p><span class="yellow">特色产业：</span> 制造、互联网</p>
                  <p class="yellow">整体情况：</p>
                  <p class="pd-10">第一产业占比20%</p>
                  <p class="pd-10">第二产业占比30%</p>
                </div>
              `;
            }
            return `<div style="padding:5px;">${params.name}</div>`;
          },
        },
        geo: {
          map: "china",
          roam: true,
          zoom: 1.2,
          label: {
            show: true,
            color: "#99ffcc",
            // color: "#7fffd4",
            fontSize: 14,
          },
          itemStyle: {
            areaColor: {
              type: "linear",
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [
                { offset: 0, color: "#33ff99" }, // 上浅
                { offset: 0.5, color: "#008060" }, // 上浅
                { offset: 1, color: "#003d33" }, // 下深
                // { offset: 0, color: '#99ffcc' }, // 顶部浅绿
                // { offset: 0.25, color: '#66ffb2' }, // 柔和亮绿
                // { offset: 0.5, color: '#00cc88' },  // 主绿色
                // { offset: 0.75, color: '#008060' }, // 稍深
                // { offset: 1, color: '#003d33' }     // 最底层深绿
              ],
              global: false,
            },
            borderColor: "#00ffaa",
            borderWidth: 2,
            shadowColor: "rgba(0,255,150,0.5)",
            shadowBlur: 80,
          },
          emphasis: {
            label: {
              color: "#fff",
            },
            itemStyle: {
              areaColor: "#003d33",
              shadowBlur: 25,
              shadowColor: "rgba(0,255,150,0.8)",
            },
          },
          select: {
            // 点击选中态
            label: { color: "#fff" },
            itemStyle: {
              areaColor: "#003d33",
              borderColor: "#fff",
              borderWidth: 2,
              shadowBlur: 40,
              shadowColor: "rgba(0, 255, 200, 0.9)",
            },
          },
        },
        series: [
          {
            type: "map",
            map: "china",
            label: {
              show: true,
            },
            select: {
              itemStyle: {
                color: "#FF0000",
              },
            },
            itemStyle: {
              normal: {
                borderColor: "#6becf5",
                color: "#fff",
              },
              emphasis: {
                areaColor: "#f60",
              },
            },
            geoIndex: 0,
            data: [
              { name: "新疆", value: 100 },
              { name: "山东", value: 80 },
              { name: "安徽", value: 60 },
              { name: "河南", value: 60 },
            ],
          },
          // 动态闪烁效果点
          {
            type: "effectScatter",
            coordinateSystem: "geo",
            symbolSize: 10,
            showEffectOn: "render",
            rippleEffect: {
              brushType: "stroke",
              scale: 8, // 波动半径
              period: 8, // 波动速度
            },
            itemStyle: {
              color: "#00ffcc",
              shadowBlur: 10,
              shadowColor: "#00ffcc",
            },
            zlevel: 3,
            data: [
              {
                name: "新疆",
                value: [87.617733, 43.792818], // 乌鲁木齐经纬度
              },
            ],
          },
        ],
      };
      this.chartRef?.clear();
      this.chartRef?.setOption(option, true);
      this.bindEvents();
    },
    bindEvents() {
      this.chartRef.on("click", (params) => {
        if (params.componentType === "geo") {
          if (!["新疆", "河南", "山东", "安徽"].includes(params.name)) {
            return alert("后台数据正在同步中,请过后再试!");
          }
          // 取消上一个选中
          if (this.selectedProvince && this.selectedProvince !== params.name) {
            this.chartRef.dispatchAction({
              type: "geoUnSelect",
              name: this.selectedProvince,
            });
          }
          // 判断当前是否取消
          if (this.selectedProvince === params.name) {
            this.chartRef.dispatchAction({
              type: "geoUnSelect",
              name: params.name,
            });
            this.selectedProvince = null;
          } else {
            this.chartRef.dispatchAction({
              type: "geoSelect",
              name: params.name,
            });
            this.selectedProvince = params.name;
            this.$store.dispatch("setCurrentProvince", params.name);
            this.$emit("province-click", params.name); // 向父组件传事件
          }
        } else if (params.componentType === "series") {
          if (!["新疆", "河南", "山东", "安徽"].includes(params.name)) {
            return alert("后台数据正在同步中,请过后再试!");
          }
          // 取消上一次高亮
          if (this.selectedProvince) {
            this.chartRef.dispatchAction({
              type: "downplay",
              seriesName: params.seriesName,
              name: this.selectedProvince,
            });
          }

          // 高亮当前点击省份
          this.chartRef.dispatchAction({
            type: "highlight",
            seriesName: params.seriesName,
            name: params.name,
          });

          this.selectedProvince = params.name; // 更新选中省份
          this.$store.dispatch("setCurrentProvince", params.name);
          console.log("选中省份:", params.name, "对应值:", params.value);
        }
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.center-map {
  width: 100%;
  height: calc(100% - 110px);
}

::v-deep .customer_map_tooltip {
  width: 300px;
  height: 200px;
  padding: 20px !important;
  margin: 0 !important;
  background-image: url("../assets/index/map_hover_bg.png");
  background-size: cover;
  background-position: center center;

  .title {
    height: 30px;
    width: 100%;
    font-size: 20px;
    color: #f9a548;
    letter-spacing: 0;
    font-weight: bold;
    margin-bottom: 10px;
    display: flex;
    align-items: center;

    &-icon {
      width: 24px;
      height: 24px;
      background-image: url("../assets/index/warnning_icon.png");
      background-size: cover;
      background-position: center center;
      margin-right: 10px;
    }
  }

  p,
  a {
    font-size: 14px;
    color: #ffffff;
    letter-spacing: 0;
    line-height: 20px;
    font-weight: 400;

    &.second_line {
      span {
        &:last-child {
          margin-left: 30px;
        }
      }
    }

    &.pd-10 {
      padding-left: 74px;
    }
  }

  .yellow {
    color: #f9a548;
    width: 74px;
  }
}
</style>
